.MainWrapper {
    height: 29rem;
    min-width: 75rem;
    display: flex;
    justify-content: center;
    background-color: #75841C;
}

.MainBox {
    min-width: 75rem;
    max-width: 75rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.Logo {
    position: relative;
    top: 3rem;
    left: -1.5rem;
}

.LogoImg {
    width: 10rem;
}

.Copyright {
    margin-top: 2.5rem;
    color: #DDD8A7;
    font-size: 1rem;
}

.RightBox {
    display: flex;
    padding-top: 5.6rem;
    color: #DDD8A7;
}

.TopMenuItem {
    font-weight: 700;
    font-size: 0.88rem;
}

.SubLinksUl {
    font-size: 0.88rem;
    font-weight: 400;
    list-style: none;
    padding: 0;
    margin-top: 2.3rem;
}

.SubLinksLi {
    margin-bottom: 1.1rem;
}

.SubLinksA {
    color: inherit;
}

.Item1 {
    width: 6.7rem;
}

.Item2 {
    margin-left: 3rem;
}

.Item3 {
    margin-left: 3rem;
}

.Item4 {
    margin-left: 2.38rem;
}

.Item5 {
    width: 8rem;
    margin-left: 3rem;
}

.Item6 {
    margin-left: 2.75rem;
}