.MainWrapper {
    height: 35rem;
    min-width: 75rem;
    display: flex;
    justify-content: center;
}

.MainBox {
    min-width: 75rem;
    max-width: 75rem;
    display: flex;
    justify-content: space-between;
}

.Box {
    width: 23.8rem;
    height: 100%;
}

.TitleBox {
    height: 7.7rem;
    overflow: auto;
}

.Title {
    margin-top: 3.4rem;
    color: #707070;
    font-size: 1.5rem;
    font-weight: 700;
}

.Image {
    max-width: 100%;
    height: auto;
}

.ButtonBox {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 50px;
    margin-top: 3rem;
    border: 3px solid #EA7119;
    border-radius: 10px;
}

.ButtonText {
    font-size: 0.9rem;
    font-weight: 700;
    color: #EA7119;
}

.TextTitle {
    margin-bottom: 1rem;
    font-size: 1.1rem;
    font-weight: 700;
    color: #707070;
}

.TextBody {
    font-size: 1.1rem;
    color: #707070;
}

.RightImageBox {
    position: relative;
}

.ImageDateBox {
    position: absolute;
    top: 0;
    right: 0;
    width: 7.2rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.8);
}

.ImageDate {
    font-size: 0.75rem;
    font-weight: 700;
    color: #707070;
}

.VestnikButtonBox {
    display: flex;
    justify-content: space-between;
    margin-top: 2.5rem;
}

.VestnikSeeAllBox {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 11.6rem;
    height: 3.1rem;
    border: 3px solid #EA7119;
    border-radius: 10px;
}

.VestnikSeeAllText {
    font-size: 0.9rem;
    font-weight: 700;
    color: #EA7119;
}

.VestnikDownload {
    font-size: 1rem;
    text-decoration: underline;
    color: #EA7119;
}