.HeaderTop {
    height: 6.8rem;
    min-width: 75rem;
    display: flex;
    justify-content: center;
    background-color: #4F6009;
    box-shadow: 0px 10px 20px #00000029;
}

.HeaderTop__Box {
    min-width: 75rem;
    max-width: 75rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.HeaderTop__Brand {
    position: relative;
    top: 3.5rem;
    left: 0.5rem;
    text-decoration: none;
}

.HeaderTopNav__Items {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
}

.HeaderTopNav__Item {
    margin: 0 0 0 2.5rem;
}

.HeaderTopNav__Item a {
    text-decoration: none;
    color: #DDD8A7;
    font-size: 1rem;
    font-weight: 700;
}

.HeaderBottom {
    position: absolute;
    min-width: 75rem;
    display: flex;
    justify-content: center;
}

.HeaderBottom__Box {
    min-width: 75rem;
    max-width: 75rem;
    display: flex;
    justify-content: flex-end;
}

.SupportUs {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 10rem;
    height: 3rem;
    background-color: #EA7119;
    border-radius: 0 0 10px 10px;
    color: #FFFFFF;
    font-size: 0.88rem;
    font-weight: 700;
    margin-right: 2.8rem;
}

.Languages {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.5rem;
    height: 2.5rem;
    font-weight: 700;
}

.RuLang {
    color: #EA7119;
    background-color: #DDD8A7;
}

.EnLang {
    color: #707070;
    background-color: #FFFFFF;
}