.MainWrapper {
    height: 35rem;
    min-width: 75rem;
    display: flex;
    justify-content: center;
    background-color: #FAFCF2;
}

.MainBox {
    min-width: 75rem;
    max-width: 75rem;
    /* display: flex; */
    /* justify-content: space-between;
    align-items: flex-start; */
}

.Title {
    font-size: 1.5rem;
    color: #707070;
    font-weight: 700;
    margin-top: 3.5rem;
    margin-bottom: 2rem;
}

.MainBody {
    display: flex;
}

.RightBox {
    margin-left: 1.9rem;
}

.FontSize18 {
    font-size: 1.1rem;
}

.FontSize34 {
    font-size: 2.1rem;
}

.Color1 {
    color: #707070;
}

.Color2 {
    color: #EA7119;
}

.Underline {
    text-decoration: underline;
}

.Bold {
    font-weight: 700;
}

.Regular {
    font-weight: 400;
}

.mB1 {
    margin-bottom: 1.5rem;
}

.mB2 {
    margin-bottom: 0.5rem;
}

.mB3 {
    margin-bottom: 1rem;
}

.ButtonBox {
    width: 11.7rem;
    height: 3.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 3px solid #75841C;
    border-radius: 10px;
    margin-top: 4.8rem;
}

.ButtonText {
    font-size: 0.9rem;
    color: #75841C;
    font-weight: 700;
}