.MainWrapper {
    height: 46.4rem;
    min-width: 75rem;
    display: flex;
    justify-content: center;
    background-color: #FDF8E5;
}

.MainBox {
    position: relative;
    min-width: 75rem;
    max-width: 75rem;
    display: flex;
    justify-content: space-between;
}

.BannerFB_Box {
    position: absolute;
    top: -2.3rem;
    min-width: 75rem;
    height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #BDB132;
    border-radius: 10px;
}

.BannerFB_Text {
    font-size: 1.5rem;
    font-weight: 700;
    color: #FFFFFF;
}

.ButtonFB_Box {
    position: absolute;
    right: 20px;
    top: 18px;
    width: 45px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FDF8E5;
    border: 2px solid #75841C;
    border-radius: 5px;
}

.ButtonFB_Text {
    padding-top: 0.1rem;
    color: #75841C;
    font-size: 2.9rem;
    font-weight: 700;
}

.NewsMainBox {
    width: 49.4rem;
}

.TitleBox {
    height: 9.1rem;
    overflow: auto;
}

.TitleNews {
    margin-top: 6.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    color: #75841C;
}

.NewsTitleLine {
    width: 100%;
    height: 3px;
    background: #75841C;
}

.NewsListBox {
    display: flex;
    justify-content: space-between;
    margin-top: 1.5rem;
}

.NewsElementBox {
    width: 23.8rem;
}

.ImageBox {
    position: relative;
}

.Image {
    max-width: 100%;
    height: auto;
}

.ImageDateBox {
    position: absolute;
    top: 0;
    right: 0;
    width: 6.8rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.8);
}

.ImageDate {
    font-size: 0.75rem;
    font-weight: 700;
    color: #75841C;
}

.NewsShortDesc {
    margin-top: 2.5rem;
    font-size: 1.1rem;
    color: #707070;
}

.MoreNewsButtonBox {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 10rem;
    height: 3.1rem;
    border: 3px solid #75841C;
    border-radius: 10px;
}

.MoreNewsButtonText {
    font-size: 0.9rem;
    color: #75841C;
    font-weight: 700;
}

.EventsMainBox {
    width: 23.8rem;
}

.TitleEvents {
    margin-top: 6.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    color: #BDB132;
}

.EventsTitleLine {
    width: 100%;
    height: 3px;
    background: #BDB132;
}

.EventsElementBox {
    width: 23.8rem;
    margin-top: 1.5rem;
}

.EventImageDate {
    font-size: 0.75rem;
    font-weight: 700;
    color: #BDB132;
}

.MoreEventsButtonBox {
    margin-top: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 11.3rem;
    height: 3.1rem;
    border: 3px solid #BDB132;
    border-radius: 10px;
}

.MoreEventsButtonText {
    font-size: 0.9rem;
    color: #BDB132;
    font-weight: 700;
}